import { html, ifDefined } from "chrome://global/content/vendor/lit.all.mjs";

export const stylesTemplate = () => html`
  <link rel="stylesheet" href="${__chrome_styles_loader__commonStyles}" />
  <link
    rel="stylesheet"
    href="${__chrome_styles_loader__inputfieldStyles}"
  />
`;

export const editableFieldTemplate = ({
  type,
  value,
  disabled,
  required,
  onFocus,
  onBlur,
  labelL10nId,
  noteL10nId,
  inputL10nId,
}) => html`
  <label
    for="input"
    class="field-label"
    data-l10n-id=${labelL10nId}
    tabindex="-1"
  >
  </label>
  <input
    id="input"
    class="input-field"
    data-l10n-id=${ifDefined(inputL10nId)}
    type=${type}
    .value=${value}
    aria-describedby="explainer"
    ?disabled=${disabled}
    ?required=${required}
    @focus=${onFocus}
    @blur=${onBlur}
  />
  <span
    id="explainer"
    role="note"
    class="explainer text-deemphasized"
    data-l10n-id=${ifDefined(noteL10nId)}
  ></span>
`;
